@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap);
.app__header{display:flex;justify-content:space-between}.app__title{font-weight:bold;font-size:28px;line-height:37px}.app__title span{color:#9f0013}.app__menu ul{display:flex;justify-content:space-between;font-weight:bold;font-size:24px;line-height:32px}.app__menu ul li{margin:0 8px}.app__menu ul li a:hover{color:#9f0013}
.single-character{margin-top:50px;display:grid;grid-template-columns:293px 550px auto;grid-column-gap:50px;-webkit-column-gap:50px;column-gap:50px;align-items:start}.single-character__char-img{width:293px;height:293px}.single-character__name{font-weight:bold;font-size:22px;line-height:29px}.single-character__descr{font-size:18px;line-height:24px;margin-top:10px}.single-character__img{width:293px;height:450px}.single-character__price{font-weight:bold;font-size:24px;line-height:32px;color:#9f0013;margin-top:25px}.single-character__back{justify-self:end;font-weight:bold;font-size:18px;line-height:24px}.single-character__back:hover{color:#9f0013}
.char__info{padding:25px;box-shadow:0px 0px 20px rgba(0,0,0,.25);position:relative;z-index:5;background-color:#fff}.char__info-name{font-weight:bold;font-size:22px;line-height:29px;text-transform:uppercase}.char__info .skeleton{margin-top:30px}.char__basics{display:grid;grid-template-columns:150px auto;grid-column-gap:25px;-webkit-column-gap:25px;column-gap:25px}.char__basics img{width:150px;height:150px;object-fit:cover}.char__btns{margin-top:10px}.char__btns a:nth-child(2){margin-top:10px}.char__descr{margin-top:15px;font-size:14px;line-height:18px}.char__comics{font-weight:bold;font-size:18px;line-height:24px;margin-top:10px}.char__comics-list{position:relative;margin-top:10px}.char__comics-item{width:100%;padding:0px 10px;line-height:25px;box-shadow:0px 4px 4px rgba(0,0,0,.25);margin-top:10px}.char__comics-more{width:100%;line-height:25px;margin-top:10px;color:#9f0013}.char__comics-none{width:100%;line-height:25px;margin-top:10px;color:#9f0013}.char__select{font-weight:bold;font-size:18px;line-height:24px;text-align:center}
.single-comic{margin-top:50px;display:grid;grid-template-columns:293px 550px auto;grid-column-gap:50px;-webkit-column-gap:50px;column-gap:50px;align-items:start}.single-comic__img{width:293px;height:450px}.single-comic__name{font-weight:bold;font-size:22px;line-height:29px}.single-comic__descr{font-size:18px;line-height:24px;margin-top:25px}.single-comic__price{font-weight:bold;font-size:24px;line-height:32px;color:#9f0013;margin-top:25px}.single-comic__back{justify-self:end;font-weight:bold;font-size:18px;line-height:24px}.single-comic__back:hover{color:#9f0013}
.skeleton__header{display:grid;grid-template-columns:40px auto;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;align-items:center}.skeleton__circle{width:40px;height:40px;background-color:#c4c4c4;border-radius:100%}.skeleton__mini{width:100%;height:16px;background-color:#c4c4c4}.skeleton__block{height:35px;width:100%;background-color:#c4c4c4;margin-top:15px}
.error{display:block;width:250px;height:250px;object-fit:contain;margin:0 auto}
.app__banner{width:100%;background-color:#232222;height:100px;padding:0 25px 0 45px;display:grid;grid-template-columns:152px auto 133px}.app__banner-text{font-weight:bold;font-size:24px;line-height:32px;color:#fff;padding-left:83px;padding-top:18px}
.button{min-width:101px;color:#fff;text-align:center;text-transform:uppercase;font-size:14px;transition:.3s transform;border:none;background-color:rgba(0,0,0,0);cursor:pointer}.button__long{display:block;width:170px;margin:45px auto 0 auto}.button .inner{position:relative;background-color:#9f0013;line-height:18px;padding:0 18px;transition:none}.button .inner::before{border-color:#9f0013 rgba(0,0,0,0);border-style:solid;border-width:0 0 10px 10px;content:"";display:block;position:absolute;left:0;top:-10px;transition:none}.button .inner::after{border-color:#9f0013 rgba(0,0,0,0);border-style:solid;border-width:0 0 10px 10px;content:"";display:block;position:absolute;right:0;bottom:-10px;-webkit-transform:rotate(180deg);transform:rotate(180deg);transition:none}.button__main:hover,.button__secondary:hover{color:#fff}.button__secondary .inner{background-color:#5c5c5c}.button__secondary .inner::before{border-color:#5c5c5c rgba(0,0,0,0);transition:none}.button__secondary .inner::after{border-color:#5c5c5c rgba(0,0,0,0);transition:none}.button::before{content:"";display:block;height:10px;margin-left:10px;transition:none}.button::after{content:"";display:block;height:10px;margin-right:10px;transition:none}.button.button__main::before,.button.button__main::after{background-color:#9f0013}.button.button__secondary::before,.button.button__secondary::after{background-color:#5c5c5c}.button:hover{-webkit-transform:translateY(-5px);transform:translateY(-5px)}.button:disabled{-webkit-filter:grayscale(0.5);filter:grayscale(0.5)}*{font-family:"Roboto Condensed",sans-serif;margin:0;padding:0;box-sizing:border-box}ul,li,dl{list-style-type:none;margin:0;padding:0}h1,h2,h3,h4,h5,h6{margin:0}a{text-decoration:none;color:inherit;display:inline-block}a:hover{text-decoration:none;color:inherit}p{margin:0;padding:0}iframe{display:none}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}::-webkit-scrollbar{width:5px;height:5px}::-webkit-scrollbar-track{background:#ededed;border-radius:2px}::-webkit-scrollbar-thumb{background:#232222;border-radius:5px}.app{width:1100px;margin:0 auto;padding:50px 0 50px 0;position:relative}.app .bg-decoration{position:absolute;right:-174px;bottom:-70px;z-index:-1}main{margin-top:50px;position:relative}.pulse{-webkit-animation:pulse 1.5s ease-in-out .5s infinite;animation:pulse 1.5s ease-in-out .5s infinite}@-webkit-keyframes pulse{0%{opacity:1}50%{opacity:.4}100%{opacity:1}}@keyframes pulse{0%{opacity:1}50%{opacity:.4}100%{opacity:1}}
